// http://msearchcdn.kugou.com/new/app/i/search.php?student=0&cmd=302&keyword=你好&with_res_tag=1
import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
import songList from "@/components/song-list/song-list.vue"
import {
	getHotSearch,
	getSearchKey,
	getSongSearch,
	getSearchKey2
} from '@/utils/api.js'
import {
	debounce
} from '@/utils/lib.js';
export default {
	components: {
		uniNavBar,
		songList
	},
	data() {
		return {
			isHistory:false,//是否有本地历史
			list: [{}],
			hotKeyArr: [],
			totalSongs: '',
			totalPage: '',
			listData: [],
			key: '',
			hotInfo:[],
			isShowPage:false,
			keyValue:'',
			storageKeyArr:[]
		}
	},
	onLoad() {
		this.getHistory();
		this.getHotSearchData();
		this.$watch('key', debounce(() => {
			// this.getSearchKeyData(this.key);
		},300))
	},
	methods: {
		back() {
			uni.navigateBack();
		},
		delKey(){
			uni.removeStorage({
			    key: 'history',
			    success: (res)=> {
					uni.showModal({
					    title: '提示',
					    content: '确定删除吗？',
					    success: function (res) {
					        if (res.confirm) {
					            this.isHistory=false;
								this.storageKeyArr=[];
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
			        
			    }
			});
		},
		getHistory(){
			return new Promise((reslove, reject) => {
				uni.getStorage({
			    key: 'history',
			    success: (res)=> {
			        console.log('history',res);
					if(res.data.length!=0){
						console.log('进来');
						this.storageKeyArr=res.data;
						reslove(res.data);
						this.isHistory=true;
					}else this.isHistory=false;
			    },
				fail:(err)=>{
					this.isHistory=false;
					console.log('err',err);
				}
			});
			});
		},
		
		getHotSearchData() {
			getHotSearch().then((res) => {
				this.hotKeyArr = res.data.data.info;
				this.getSearchData('热门歌曲').then(res=>{
					this.hotInfo=res.data.data.info;
				})
			})
		},
		clickSong(item){
			
		},
		toSearch(){
			console.log('dddd');
			this.getSearchData().then(res=>{
				this.listData=res.data.data.info;
			})
			this.setKey(this.keyValue)
		},
		setKey(key){
			this.storageKeyArr.push(key);
			this.storageKeyArr=Array.from(new Set(this.storageKeyArr));
			uni.setStorage({
			    key: 'history',
			    data: this.storageKeyArr,
			    success:()=> {
			        console.log('success');
					this.isHistory=true;
			    }
			});
		},
		onSearch(key) {
			console.log('key',key);
			this.isShowPage=true;
			this.key=key;
			this.setKey(key)
			this.getSearchData(key).then(res=>{
				this.listData=res.data.data.info;
				this.keyValue=key;
			})
		},
		searchSongInput(e) {
			console.log('e',e);
			let key=e.detail.value;
			if(key!==''){
				this.isShowPage=true;
				this.key=key;
			}else{
				this.key='';
				this.isShowPage=false;
				this.listData=[];
			}
			
		},
		getSearchKeyData(key){
			getSearchKey2({
				keyword: this.key
			}).then((res) => {
				console.log('getSearchKey',res);
				
			})
		},
		getSearchData(key) {
			uni.showLoading({
			    title: '加载中'
			});
			return new Promise((reslove, reject) => {
				getSongSearch({
					format: 'json',
					keyword: key || this.keyValue,
					page: 0,
					pagesize: 20
				}).then((res) => {
					uni.hideLoading();
					reslove(res)
				})
			})
		}
	}
}
